* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.app {
  height: 100vh;
  width: 100vw;
  background-color: #64748b;
  display: flex;
  justify-content: center;
  align-items: center;
}

button {
  font-size: 36px;
  padding: 1em 3em;
  cursor: pointer;
  border: none;
  background-color: #ecfeff;
  border-radius: 6px;
  color: #0c4a6e;
  transition: all .15s ;
}

button:hover {
  background-color: #cffafe;
  color: #111827;
}
